@Component({
  selector: 'app-register',
  templateUrl: './register.page.html',
  providers: [HttpTestService],
  styleUrls: ['./register.page.scss'],
})
export class RegisterPage implements OnInit {

  form1 = true;
  form2 = false;
  form3 = false;

  header1 = true;
  header2 = false;
  header3 = false;

  submitted = false;

  termos = false;

  onForm2() {

    this.submitted = true;
    if (this.fGroup.invalid || this.part1.invalid) {
      return;
    }
    console.log("Sucesso");

    this.form1 = false;
    this.form2 = true;
    this.form3 = false;

    this.header1 = false;
    this.header2 = true;
    this.header3 = false;
  }

  onForm3() {
    this.form1 = false;
    this.form2 = false;
    this.form3 = true;

    this.header1 = false;
    this.header2 = false;
    this.header3 = true;
  }

  changeEvent(event) {
    if (event.value) {
      this.termos = false
    }else{
      this.termos = true;
    }
}

  error_messages = { 
    'confirmpassword': [
      { type: 'required', message: 'password is required.' }
    ],
  }


  constructor(private  router:  Router, private fBuilder: FormBuilder, private modalCtrl: ModalController, private service: HttpTestService, private loadingController: LoadingController, private toastController: ToastController) {
    
   }

 

  public fGroup: FormGroup;
  public part1: FormGroup;
  public part2: FormGroup;
  current_date:string;

  private loading;
  private toast;
  
  private toastFail;
  private loadingfail;
   
  ionViewWillEnter(){
    this.current_date = moment(Date.now()).format('YYYY-MM-DD');
  }

  ngOnInit() {
    
    this.fGroup = this.fBuilder.group({
      name: new FormControl('', [Validators.required, Validators.minLength(3), Validators.pattern(/^[a-zA-Z\'\-\u00C0-\u00FF].*[\]+[a-zA-Z\'\-\u00C0-\u00FF].*$/)]),
      email: new FormControl('', [Validators.required, Validators.email]),
      password: new FormControl('', [Validators.required, Validators.minLength(6)]),
      password_confirmation: new FormControl('', Validators.required),
      
      
      "client_detail_attributes"  :[
        this.part1 = this.fBuilder.group({
          gender: new FormControl('', Validators.required),
          cardiac_disease: new FormControl(''),
          diabetes_disease: new FormControl(''),
          cancer_disease: new FormControl(''),
          birthdate: new FormControl('', Validators.required),
        })
      ],
      "client_physicals_attributes"  :[
        this.part2 = this.fBuilder.group({
          height_m: new FormControl(''),
          weight_kg: new FormControl(''),
          waist_cm: new FormControl(''),
        })
      ]
    }, {
      validators: this.password.bind(this)
    });
  
    console.log(this.fGroup.value)
  //   this.fGroup = new FormGroup({
  //     name: new FormControl(''),
  //     email: new FormControl(''),
  //     password: new FormControl(''),
  //     password_confirmation: new FormControl(''),
  //     client_detail_attributes:this.part1,
  //     client_physicals_attributes:this.part2
  //   });
  //   this.part1 = new FormGroup({
      
    
  //   });
  //   this.part2 = new FormGroup({
  //     height_m: new FormControl(''),
  //     weight_kg: new FormControl(''),
  //     waist_cm: new FormControl(''),
      
  //   });

  }

  async showModal() {
    const modal = await this.modalCtrl.create({
      component: ModalcinturaPage,
     cssClass:'cintura_modal'
      
    });
    modal.present();
  }

  async showModalTermos1() {

  const modal = await this.modalCtrl.create({
    component: Modaltermos1Page,
    cssClass:'termos_modal'
      
  })
  modal.present();
  }

  submitForm() {
    


    this.part1.value.gender = parseInt(this.part1.value.gender)
    this.part1.value.cardiac_disease = parseInt(this.part1.value.cardiac_disease)
    this.part1.value.diabetes_disease = parseInt(this.part1.value.diabetes_disease)
    this.part1.value.cancer_disease = parseInt(this.part1.value.cancer_disease)  
    //console.log(this.part2.value.height_m.replace(",", "."));
    if (this.part2.value.height_m){
      this.part2.value.height_m = this.part2.value.height_m.replace(",", ".")
    }
    if(this.part2.value.weight_kg){
      this.part2.value.weight_kg = this.part2.value.weight_kg.replace(",", ".")
    }
    if(this.part2.value.waist_cm){
      this.part2.value.waist_cm = this.part2.value.waist_cm.replace(",", ".")
    }
    
    //console.log(this.part2.value.height_m);
    /*Criação do loading e toast*/ 
    this.loadingController.create ({
      message: 'Criando sua conta...'
    }).then((overlay) =>{
      this.loading = overlay;
      this.loading.present();
    });

    this.toastController.create ({
      message: 'Conta criada com sucesso, faça o login.',
      duration: 4000,
      color: 'success',
      position: 'top'
    }).then((overlay) =>{
      this.toast = overlay;    
    });

    this.toastController.create ({
      message: 'Ocorreu um erro ao criar sua conta, tente novamente mais tarde.',
      duration: 4000,
      color: 'warning',
      position: 'top'
    }).then((overlay) =>{
      this.toastFail = overlay;    
    });
    
  /*Fim da criação do loading e toast*/ 
    this.service.register('api/auth', this.fGroup,this.part1,this.part2).subscribe(data => {
      console.log(data.body);
      console.log(data)
      if (data.status === 200 || data.status === 201){
      this.router.navigateByUrl('login');
       this.loading.dismiss();
       this.toast.present();
      }else {
      }
    }, error => {
      //console.log("teste valendo junior");
      this.loading.dismiss();
      this.toastFail.present();
    });
    
  }

   // convenience getter for easy access to form fields
   get f() { return this.fGroup.controls; }

  password(formGroup: FormGroup) {
    const { value: password } = formGroup.get('password');
    const { value: password_confirmation } = formGroup.get('password_confirmation');
    return password === password_confirmation ? null : { passwordNotMatch: true };
  }

}